import React, { Component } from "react"
import PacMan from "../loader/pacMan"
import axios from "axios"
import { config } from "../../mainConfig"
import { Bar, BarChart, Cell, ResponsiveContainer, YAxis } from "recharts"

class Runtime extends Component {
  constructor(props) {
    super(props)
    this.platform = this.props.platform
    this.type = this.props.type
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getRuntime = this.getRuntime.bind(this)
  }

  componentDidMount() {
    this.getRuntime()

    this.updateMetric = setInterval(this.getRuntime, config.updateInterval)
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getRuntime() {
    axios
      .get(`http://127.0.0.1:8000/api/logs/import/runtime`, {
        params: {
          platform: this.platform,
          type: this.type,
        },
      })
      .then((response) => {
        this.setState({ data: response.data, loading: false })
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false })
      })
  }

  maybeLater() {
    return this.state.data.map((entry, index) => {
      let color
      if (entry.runtime < 50) {
        color = "green"
      } else if (entry.runtime < 65) {
        color = "darkgreen"
      } else if (entry.runtime < 80) {
        color = "orange"
      } else if (entry.runtime < 90) {
        color = "darkorange"
      } else {
        color = "crimson"
      }
      return <Cell fill={color} />
    })
  }

  renderShape =
    (key, pixel = 10) =>
    ({ height, width, fill, x, y, ...rest }) => {
      const xpercent = Math.trunc((pixel * 100) / Math.trunc(height || 1))
      return (
        <svg x={x} y={y} fill="none" xmlns="http://www.w3.org/2000/svg">
          <defs>
            <linearGradient id={key} x1="0" y1="0" x2="0" y2={`${xpercent}%`}>
              <stop
                offset="0%"
                style={{ stopColor: "crimson", stopOpacity: 1 }}
              />
              <stop
                offset="25%"
                style={{ stopColor: "yellow", stopOpacity: 1 }}
              />
              <stop
                offset="100%"
                style={{ stopColor: "green", stopOpacity: 1 }}
              />
            </linearGradient>
            <clipPath id="clip1">
              <rect width={`${width}`} height={`${height}%`} y={`${y}`} />
            </clipPath>
          </defs>
          <rect fill={`url(#${key})`} width={width} height={height} />
        </svg>
      )
    }

  renderBar() {
    return (
      <ResponsiveContainer width={this.props.width} height={this.props.height}>
        <BarChart
          width={this.props.width}
          height={this.props.height}
          data={this.state.data}
        >
          <YAxis
            tickFormatter={(tick) => `${tick}%`}
            type="number"
            domain={[0, 100]}
          />
          <Bar
            fill="green"
            dataKey="runtime"
            shape={this.renderShape("runtime")}
            stackId="a"
          />
        </BarChart>
      </ResponsiveContainer>
    )
  }

  philipMethod() {
    return (
      <div
        className="health_container"
        style={{
          width: "100%",
          height: "50px",
          position: "relative",
          background:
            "linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 25%, rgba(26,255,0,1) 100%)",
        }}
      >
        <div
          className="health_bar"
          style={{
            height: "30px",
            width: `${this.state.data.runtime}%`,
            position: "absolute",
            right: 0,
            top: 0,
            backgroundColor: "lightgrey",
            zIndex: 1,
          }}
        />
      </div>
    )
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return this.renderBar()
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default Runtime
