import React, { useState } from "react";
import Runtime from "./graphs/runtime";

function Runtimes(props) {
  const [runtimes] = useState([...props.data]);
  return (
    <>
      {runtimes.map((runtime, index) => {
        return (
          <Runtime
            key={"runtime-" + index}
            platform={props.platform}
            type={runtime.type}
            text={runtime.text}
            width={200}
            height={150}
          />
        );
      })}
    </>
  );
}

export default Runtimes;
