import React, { useState } from "react";
import Average from "./graphs/average";

function Averages(props) {
  const [averages] = useState([...props.data]);
  return (
    <>
      {averages.map((average, index) => {
        return (
          <Average
            key={"average-" + index}
            platform={props.platform}
            type={average.type}
            text={average.text}
          />
        );
      })}
    </>
  );
}

export default Averages;
